<template>
  <div id="Mv">
    <div class="Mv_top">
      <img class="imgTop" :src="Mv.cover" alt="">
      <div><van-button style="opacity: 0.2;" icon="arrow-left" size="mini" plain @click="onClickTopLeft"/></div>
      <video ref="mv" controls autoplay name="media">
        <source :src="Mv.url" type="video/mp4">
      </video>
    </div>
    <div class="Mv_main">
        <div class="des">
            <div>
                <h3>{{Mv.name}}</h3>
                <h5>{{getArtists(Mv.artists)}}</h5>
            </div>
            <span>{{Mv.desc}}</span>
        </div>
        <h4>精彩评论</h4>
        <Comment v-for="(item,i) in Mv.comment" :key="i"
        :content="item.content"
        :timeStr="item.timeStr"
        :userImg="item.user.avatarUrl"
        :nickname="item.user.nickname"
        :likedCount="item.likedCount"
        ></Comment>
    </div>
    <div style="width:100%;height:1rem"></div>
  </div>
</template>

<script>
export default {
data(){
    return{
        Mv:{},
    }
},
methods:{
    async getMv(){
        const {data :res} = await this.$http.get('/mv/detail?mvid='+this.$route.params.id)
        console.log(res)
        this.Mv=res.data
        this.getMvUrl(res.data.id)
        this.getMvComment(res.data.id)
    },
    async getMvUrl(id){
        const {data :res} = await this.$http.get('/mv/url?id='+id)
        console.log(res.data.url)
        this.Mv.url=res.data.url
        this.$refs.mv.src=res.data.url
    },
    async getMvComment(id){
        const {data :res} = await this.$http.get('/comment/mv?id='+id)
        console.log(res)
        this.Mv.comment=res.hotComments
    },
    getArtists(x){
      var a=''
      for(let i in x){
        a+=x[i].name+'/'
      }
      return a
    },
    onClickTopLeft(){
        this.$router.back()
    }
},
created(){
    this.getMv();
}
}
</script>

<style lang="less" scoped>
#Mv{
    .Mv_top{
        img{
            width: 100%;
            position: absolute;
            z-index: -1;
            filter: blur(0.4rem);
        }
        video{
            width: 100%;
        }
    }
    .Mv_main{
        .des{
            width: 100%;
            height: 2rem;
            overflow: auto;
        }
        h3,h5{
            display: inline-block;
            margin-right: 0.2rem;
        }
    }
}
</style>